import React, { useContext } from 'react';
import GenerateSoldOut from './GenerateSoldOut';
import GenerateBlock from './GenerateBlock';
import { appStore } from '../../state/app';
import useBuy from '../../hooks/useBuy';

const Generate = () => {
  const { state } = useContext(appStore);
  const { soldOut } = state.app;
  const { oneNFT } = state.price;

  const { formatPrice } = useBuy();

  return (
    <section className="generate" id="generate">
      <div className="generate__container">
        <div className="generate__information">
          <h2 className="generate__title">$NEARCollectibles</h2>
          <p className="generate__text">
            Releasing collection of 2,222 <strong>Cute Penguins</strong> to the $NEAR Blockchain
          </p>
          <p className="generate__text">
            NEARCollectibles are priced at a flat rate of {formatPrice(oneNFT)}{' '}
            NEAR.
          </p>
          <h3 className="generate__title">Pre-Sale Limit 3</h3>
        </div>

        <div className="generate__block">
          {soldOut ? <GenerateSoldOut /> : <GenerateBlock />}

          <picture>
            <source srcSet="./images/generate-penguins.webp 2x, ./images/generate-penguins.webp 1x" />
            <img
              className={`generate__img ${
                soldOut ? 'generate__img-sold-out' : ''
              }`}
              src="./images/generate-penguins.webp"
              alt="generate misfits"
            />
          </picture>
        </div>
      </div>
    </section>
  );
};

export default Generate;
